import React from 'react'
import css from './footer.css'

class Footer extends React.Component {
    render () {
      return <div className={css.footer}>
        <div>
          logo
        </div>
        <div>
          <img className={css.pin}src="data:image/png;base64,"/>
          <img className={css.phone}src="data:image/png;base64,"/>          <a>email</a>
          <img className={css.mail}src="data:image/png;base64,"/>
        </div>
        <div>
          rights reserved
        </div>
      </div>
    }
  }

  export default Footer